import React from "react";
import useForm from "react-hook-form";
import axios from "axios";

function Form() {
  const { register, handleSubmit, errors, watch, reset } = useForm();

  let onSubmit = ({ name, email, password, confirm }) => {
    axios.post("/api/form", {
      name,
      email,
      password,
      confirm
    });
    reset();
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <input
        type="text"
        name="name"
        placeholder="Enter name"
        ref={register({ required: true })}
      />

      {errors.name && errors.name.type === "required" && (
        <p>Name is required</p>
      )}

      <input
        type="text"
        name="email"
        placeholder="Example@youremail.com"
        ref={register({ required: true, pattern: /^\S+@\S+\.\S+$/ })}
      />

      {errors.email && errors.email.type === "required" && (
        <p>E-mail is required</p>
      )}

      {errors.email && errors.email.type === "pattern" && (
        <p>E-mail provided is invalid</p>
      )}

      <input
        type="password"
        name="password"
        placeholder="Enter a password"
        ref={register({ required: true, minLength: 6 })}
      />

      {errors.password && errors.password.type === "required" && (
        <p>Password is required</p>
      )}

      {errors.password && errors.password.type === "minLength" && (
        <p>Password is too short</p>
      )}

      <input
        type="password"
        name="confirm"
        placeholder="Confirm password"
        ref={register({
          required: true,
          validate: value => value === watch("password")
        })}
      />

      {errors.confirm && errors.confirm.type === "required" && (
        <p>Confirm is required</p>
      )}

      {errors.confirm && errors.confirm.type === "validate" && (
        <p>Password doesn't match</p>
      )}

      <input type="submit" value="Register" />
    </form>
  );
}

export default Form;
